<template>
  <div class="home">
    <div
      v-show="layoutMode || (cameras != null && cameras.length == 0)"
      class="text-h6 text-center ma-10"
    >
      <v-btn @click="$router.push('setup')" color="primary">
        Add Camera
        <v-icon> mdi-camera-plus </v-icon>
      </v-btn>
    </div>

    <v-dialog
      v-model="showDialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar style="position: fixed; z-index: 1000" width="100vw">
          <v-btn icon dark @click="showDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title> Captures </v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>
        <CaptureOverview
          v-if="dialogCam != null"
          :cam="dialogCam"
        ></CaptureOverview>
      </v-card>
    </v-dialog>
    <div v-for="(cam, i) in cameraLayout" :key="i">
      <v-btn @click="moveUp(cam)" v-if="layoutMode" style="width: 100%">
        <v-icon>mdi-arrow-up-thick</v-icon>
      </v-btn>
      <VideoPlayer
        v-hold.1500="
          () => {
            longPress(cam);
          }
        "
        class="player"
        @show-files="showFiles(cam)"
        @show-settings="showSettings(cam)"
        :cam="cam"
        @playing="playing[cam.id] = true"
        :stream="streams[cam.id]"
      />
      <v-btn
        @click="moveDown(cam)"
        v-if="layoutMode"
        class="mb-5"
        style="width: 100%"
      >
        <v-icon>mdi-arrow-down-thick</v-icon>
      </v-btn>
    </div>
  </div>
</template>

<script>
import VideoPlayer from "@/components/VideoPlayer.vue";
import CaptureOverview from "@/components/CaptureOverview.vue";

import gql from "graphql-tag";

export default {
  name: "Home",
  components: {
    VideoPlayer,
    CaptureOverview
  },
  apollo: {
    $subscribe: {
      onCamerasChanged: {
        query: gql`
          subscription {
            onCamerasChanged {
              name
              id
              isObjectDetectionEnabled
              streamUrl
              user
            }
          }
        `,
        async result({ data }) {
          this.cameras = data.onCamerasChanged;
          this.streams = [];
          for (const cam of this.cameras) {
            let stream = await this.WEBRTC_CONNECT(cam);

            if (this.ADAPTER.browserDetails.browser == "safari") {
              this.$nextTick(async () => {
                setTimeout(async () => {
                  await this.sleep(5000);
                  if (!this.playing[cam.id]) {
                    let retry = await this.WEBRTC_CONNECT(cam);
                    console.log("RETRY: " + cam.id);
                    await this.sleep(100);
                    this.$set(this.streams, cam.id, retry);
                  }
                  await this.sleep(5000);
                  if (!this.playing[cam.id]) {
                    let retry = await this.WEBRTC_CONNECT(cam);
                    console.log("RETRY: " + cam.id);
                    await this.sleep(100);
                    this.$set(this.streams, cam.id, retry);
                  }
                }, 5 + this.getRandomInt(500));
              });
            } else {
              this.$set(this.streams, cam.id, stream);
            }
          }
        }
      }
    }
  },
  computed: {
    cameraLayout() {
      if (this.layout == null) {
        return this.cameras;
      }

      let extra = [];
      let removed = [];

      if (this.cameras != null) {
        for (const cam of this.cameras) {
          if (!this.layout.some(c => c.id == cam.id)) {
            this.layout.push(cam);
          }
        }

        this.layout = this.layout.filter(cam =>
          this.cameras.some(c => c.id == cam.id)
        );
      }

      return this.layout;
    }
  },
  mounted() {
    let result = localStorage.getItem("layout");

    if (result != null) {
      this.layout = JSON.parse(result);
    }
  },
  methods: {
    moveUp(cam) {
      let copy = this.cameraLayout.slice();

      let index = copy.indexOf(cam);

      copy.splice(index, 1);
      copy.splice(index - 1, 0, cam);

      this.layout = copy;

      localStorage.setItem("layout", JSON.stringify(this.layout));
    },
    moveDown(cam) {
      let copy = this.cameraLayout.slice();

      let index = copy.indexOf(cam);

      copy.splice(index, 1);
      copy.splice(index + 1, 0, cam);

      this.layout = copy;

      localStorage.setItem("layout", JSON.stringify(this.layout));
    },
    longPress(cam) {
      this.layoutMode = !this.layoutMode;
    },
    getRandomInt(max) {
      return Math.floor(Math.random() * Math.floor(max));
    },
    async sleep(milliseconds) {
      return new Promise(resolve => setTimeout(resolve, milliseconds));
    },
    showFiles(cam) {
      this.dialogCam = cam;
      this.showDialog = true;
    },
    showSettings(cam) {
      this.$router.push({
        name: "Setup",
        params: {
          cam: cam
        }
      });
    }
  },
  data: () => ({
    layout: null,
    layoutMode: false,
    showDialog: false,
    dialogCam: null,
    streams: [],
    cameras: null,
    playing: []
  })
};
</script>

<style scoped>
.addBtn {
  position: absolute;
  user-select: none;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
}

.player {
  display: flex;
}

